@opacity: 0.65;
@height: 44px;

.hover-mixin() {
  opacity: @opacity;
  transition: opacity 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.navbar {
  margin-bottom: 0;
  border: 0;
}

.navbar-default {
  height: @height;
  background: rgba(0, 0, 0, 0.8) none;
  color: white;
  border-radius: 0;
  box-shadow: 0;
}

.navbar-brand {
  height: 44px;
  padding: 11px 15px;
}

.navbar-nav > li > a {
  color: white;
  font-size: 16px;
  text-transform: uppercase;
}

.nav > li > a:focus, .nav > li > a:hover {
  .hover-mixin();
  text-decoration: none;
  background-color: transparent;
}

.social {
  img {
    width: 25px;
  }
}

@media (min-width: 768px) {
  .navbar-nav > li > a {
    padding-top: 14px;
  }

  .navbar-nav > li > a.social {
    padding-top: 9px;
  }
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: #FFF;
}

.navbar-default .navbar-toggle {
  border-color: #FFF;
}

.navbar .nav > li {
  z-index: 1001;
}

.navbar-nav {
  margin: 0 -15px;
}

.navbar-default .navbar-collapse, .navbar-default .navbar-form {
  border-color: transparent;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
  background-color: #636363;
}

.navbar-collapse {
  border-top: 0;
}

.navbar-brand {
  margin-right: 20px;
}

.navbar-default .navbar-nav > li > a {
  color: white;
}

.navbar-default .navbar-nav > li > a:focus, .navbar-default .navbar-nav > li > a:hover {
  color: white;
  background-color: transparent;
}

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .open > a {
  background: none;
  box-shadow: none;
  text-decoration: underline;
}

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:focus,
.navbar-default .navbar-nav > .active > a:hover {
  color: white;
  background: none;
}

.navbar-default .in .navbar-nav > .active > a,
.navbar-default .in .navbar-nav > .active > a:focus,
.navbar-default .in .navbar-nav > .active > a:hover {
  color: white;
  text-decoration: underline;
  background-color: #333;
  text-align: center;
}

.navbar-default .in .navbar-nav a,
.navbar-default .in .navbar-nav a:focus,
.navbar-default .in .navbar-nav a:hover {
  color: white;
  background-color: #333;
  text-align: center;
}

.in .nav > li > a:focus, .in .nav > li > a:hover {
  opacity: 1;
  background-color: lighten(#333, 20%) !important;
}

.collapsing {
  position: relative;
  height: 0;
  -webkit-transition: height .1s ease;
  -o-transition: height .1s ease;
  transition: height .1s ease;
}